:root {
  --color-text: 49, 62, 81;
  --color-text--secondary: 98, 108, 127;
  --color-bg: 255, 255, 255;
  --color-quiz: 255, 255, 255;
  --color-quiz--icon: 0, 0, 0;
  --color-option: 167, 41, 245;
  --color-error: 238, 84, 84;
  --color-correct: 38, 215, 130;
