}

html {
  font-family: "Rubik", sans-serif;
  font-size: 62.5%;
}

body {
  background-image: var(--img-url);
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-color: rgb(var(--color-bg));
  color: rgb(var(--color-text));
  display: grid;
  grid-template-rows: 25% 1fr 15%;
  min-height: 100dvh;
  overflow-x: hidden;
  padding: 0 10%;
  width: 100%;
}
body .dark {
  color-scheme: dark;
}
body .light {
  color-scheme: light;
}

.header {
  align-items: center;
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.header__subject {
  align-items: center;
  display: flex;
  gap: 3.2rem;
}
.header__subject img {
  background-color: rgba(var(--color-quiz--icon), 0.05);
  border-radius: 8px;
  padding: 0.8rem;
}
.header__subject p {
  font-size: 2.8rem;
}

.theme {
  align-items: center;
  display: flex;
  gap: 1.6rem;
}
.theme__icon--sun {
  background-image: var(--img-icon--sun);
  background-repeat: no-repeat;
  height: 2.4rem;
  width: 2.4rem;
}
.theme__icon--moon {
  background-image: var(--img-icon--moon);
  background-repeat: no-repeat;
  height: 2.4rem;
  width: 2.4rem;
}
.theme__btn {
  background-color: rgb(var(--color-option));
  cursor: pointer;
  height: 2.8rem;
  position: relative;
  width: 4.8rem;
  border-radius: 16px;
}
.theme__btn::before {
  background-color: white;
  border-radius: 100%;
  content: "";
  height: 2rem;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  transition: left 0.3s ease, right 0.3s ease;
  width: 2rem;
}

.dark .theme__btn::before {
  right: 3px;
}

.light .theme__btn::before {
  left: 3px;
}

.container {
  display: none;
}
.container.show {
  color: rgb(var(--color-text));
  display: grid;
  gap: 3.2rem;
  grid-template-columns: 1fr 1fr;
  justify-content: center;
}

.home {
  display: flex;
  flex-direction: column;
  gap: 4rem;
}
.home h1 {
  font-size: 6.4rem;
}
.home p {
  color: rgb(var(--color-text--secondary));
  font-size: 2rem;
}

.subjects {
  display: flex;
  flex-direction: column;
  gap: 3.2rem;
}
.subjects__subject {
  align-items: center;
  background-color: rgb(var(--color-quiz));
  border-radius: 8px;
  box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.1);
  border: 3px solid transparent;
  cursor: pointer;
  display: flex;
  gap: 3.2rem;
  padding: 1.2rem;
}
.subjects__subject:hover {
  border: 3px solid rgba(var(--color-option), 0.5);
  color: rgba(var(--color-option), 0.5);
  transition: all 0.3s ease;
}
.subjects__subject:focus, .subjects__subject:active {
  border: 3px solid rgba(var(--color-option), 1);
  color: rgba(var(--color-option), 1);
}
.subjects__subject img {
  background-color: rgba(var(--color-quiz--icon), 0.05);
  border-radius: 8px;
  padding: 0.8rem;
}
.subjects__subject p {
  font-size: 2.8rem;
}

.quiz {
  display: none;
}
.quiz.show {
  color: rgb(var(--color-text));
  display: grid;
  gap: 3.2rem;
  grid-template-columns: 1fr 1fr;
  justify-content: center;
}
.quiz__home {
  display: flex;
  flex-direction: column;
  gap: 4rem;
}
.quiz__home h2 {
  font-size: 6.4rem;
}
.quiz__home p {
  color: rgb(var(--color-text--secondary));
  font-size: 2rem;
}
.quiz__answers {
  display: flex;
  flex-direction: column;
  gap: 3.2rem;
}
.quiz__option {
  background-color: rgba(var(--color-quiz--icon), 0.05);
  border-radius: 8px;
  font-weight: bold;
  padding: 1.6rem;
}
.quiz__answer {
  align-items: center;
  background-color: rgb(var(--color-quiz));
  border-radius: 8px;
  border: 1px solid transparent;
  box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  display: flex;
  gap: 3.2rem;
  padding: 1.2rem;
}
.quiz__answer.active {
  border: solid 1px rgb(var(--color-option));
}
.quiz__answer.error {
  border: solid 1px rgb(var(--color-error));
}
.quiz__answer.correct {
  border: solid 1px rgb(var(--color-correct));
}
.quiz__answer:not(.active):hover .quiz__option {
  background-color: rgba(var(--color-option), 0.1);
  color: rgb(var(--color-option));
  transition: all 0.3s ease;
}
.quiz__answer.active .quiz__option {
  background-color: rgba(var(--color-option), 1);
  color: white;
  transition: all 0.3s ease;
}
.quiz__answer.error .quiz__option {
  background-color: rgb(var(--color-error));
}
.quiz__answer.correct .quiz__option {
  background-color: rgb(var(--color-correct));
}
.quiz__answer.disabled {
  pointer-events: none;
}
.quiz__answer p {
  font-size: 1.6rem;
}
.quiz__error {
  align-items: center;
  display: flex;
  gap: 3.2rem;
  justify-content: center;
}
.quiz__error p {
  color: rgb(var(--color-error));
  font-size: 2.4rem;
}

.result {
  display: none;
}
.result.show {
  color: rgb(var(--color-text));
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.result__home {
  display: flex;
  flex-direction: column;
  font-size: 6.4rem;
  gap: 1.2rem;
}
.result__home h3 {
  font-weight: bold;
}
.result__title {
  align-items: center;
  display: flex;
  gap: 3.2rem;
}
.result__title span {
  font-size: 2.8rem;
}
.result__title img {
  background-color: rgba(var(--color-quiz--icon), 0.05);
  border-radius: 8px;
  padding: 0.8rem;
}
.result__info {
  align-items: center;
  display: flex;
  flex-direction: column;
  gap: 3.2rem;
}
.result__box {
  align-items: center;
  border-radius: 8px;
  box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.1);
  background-color: rgb(var(--color-quiz));
  display: flex;
  flex-direction: column;
  gap: 1.6rem;
  padding: 3.2rem;
  width: 100%;
}
.result__length {
  font-size: 2.4rem;
}
.result__points {
  font-size: 14.4rem;
  font-weight: bold;
}

.btn {
  color: white;
  border-style: none;
  background-color: rgb(var(--color-option));
  border-radius: 8px;
  cursor: pointer;
  font-size: 2.8rem;
  height: 9.2rem;
  outline: none;
  width: 100%;
}
.btn:hover {
  filter: brightness(1.2);
  transition: all 0.3s;
}
.btn:active {
  border-style: none;
  filter: brightness(1.3);
  transition: all 0.3s;
}

body.dark {
  --color-text: 255, 255, 255;
  --color-text--secondary: 171, 193, 225;
  --color-bg: 49, 62, 81;
  --color-quiz: 59, 77, 102;
  --color-quiz--icon: 255, 255, 255;
  --img-url: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
  --img-icon--sun: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
  --img-icon--moon: url(${___CSS_LOADER_URL_REPLACEMENT_5___});
}

@media screen and (max-width: 1000px) {
  .container.show {
    color: rgb(var(--color-text));
    display: grid;
    gap: 3.2rem;
    grid-template-columns: 1fr;
    grid-template-rows: max-content 1fr;
  }
  .home h1 {
    font-size: 4rem;
  }
  .quiz.show {
    grid-template-columns: 1fr;
    grid-template-rows: max-content 1fr;
    gap: 3.2rem;
  }
  .quiz__home h2 {
    font-size: 4rem;
  }
  .result.show {
    grid-template-columns: 1fr;
    grid-template-rows: max-content 1fr;
    gap: 3.2rem;
  }
